<template>

  <el-container>
    <el-header style="height:160px">
      <el-row :gutter="20">

        <el-col :span="6">
          <wedit v-model="key0" ref="keyfld" input-hint="请输入key"></wedit>
        </el-col>
        <el-col :span="6">
          <wselect v-model="keyType" input-hint="请选择key类型" list-name="icbcKeyType"></wselect>
        </el-col>

        <el-col :span="6">
          <mselect v-model="models" split-word="|" input-hint="请选择model类型" list-name="icbcSearchModel"></mselect>
        </el-col>


        <el-col :span="6">

          <wbutton  task-name="icbcSearch" :task-para="getInfoTaskPara" @taskFinish="taskFinish">查询</wbutton>

        </el-col>

      </el-row>
      <el-row :gutter="20">

        <el-col :span="6">
          <wedit v-model="filter.areaCode" ref="areaCode" input-hint="请输入区域代码"></wedit>
        </el-col>
        <el-col :span="6">
          <wedit v-model="filter.industryPhy" ref="industryPhy" input-hint="请输入行业代码"></wedit>
        </el-col>
        <el-col :span="6">
          <wedit v-model="filter.industryPhy" ref="industryPhy" input-hint="请输入行业代码"></wedit>
        </el-col>

        <el-col :span="6">
          <wedit v-model="filter.entStatus" ref="entStatus" input-hint="企业经营状态"></wedit>
        </el-col>
        <el-col :span="6">
          <wedit v-model="filter.regcapGte" ref="regcapGte" input-hint="最小注册资本"></wedit>
        </el-col>
        <el-col :span="6">
          <wedit v-model="filter.regcapLte" ref="regcapLte" input-hint="最大注册资本"></wedit>
        </el-col>

        <el-col :span="6">
          <wedit v-model="filter.regcapGte" ref="regcapGte" input-hint="最小注册资本"></wedit>
        </el-col>
        <el-col :span="6">
          <wedit v-model="filter.regcapLte" ref="regcapLte" input-hint="最大注册资本"></wedit>
        </el-col>


      </el-row>

    </el-header>

    <el-container>
      <div style="height:400px;width:100%;overflow:auto;">

        <el-scrollbar>
          <div v-for="(infoitem,index) in getIcbcInfo" :key="index">

            <info-show  :infoData="infoitem" :model="infoitem.MODEL"></info-show>
          </div>
        </el-scrollbar>
      </div>
    </el-container>

  </el-container>


</template>

<script>
  import wselect from "../wcontrol/wselect";
  import mselect from "../wcontrol/mselect";
  import wedit from "../wcontrol/wedit";
  import wbutton from "../wcontrol/wbutton";
  import infoShow from "./infoShow";


  export default {
    name: "search",
    components: {wselect,mselect, wedit, wbutton, infoShow},
    data() {
      return {
        keyType: "32",
        key0: "小米科技有限责任公司",
        models: "",
        taskName:"icbcInfo",
        infodata: null,
        infoinfo: null,
        filter:{
          areaCode:"",//区域代码，智能过滤用
          industryPhy:"",//行业代码，智能过滤用
          entStatus:"",//企业经营状态，智能过滤用
          regcapGte:"",//最小注册资本（万元），智能过滤用，如：0
          regcapLte:"",//最大注册资本（万元），智能过滤用，如：100
          esdateStart:"",//成立时间起，智能过滤用，如：2018-02-22，
          esdateEnd:"",//成立时间至，智能过滤用，传空默认为当前日期
          enableAggregate: "true",//是否返回统计信息
          mask:"10101000011"//全部-10101000011，企业名称-10100000000，法人-00001000000，高管-00000000010，股东-00000000001
        }
      }
    },
    created() {

    },
    computed:
      {
        getInfoTaskPara() {
          console.debug("infoTaskPara")
          // //this.models = "SHAREHOLDER|PERSON|YEARREPORTPAIDUPCAPITAL"
          // this.models = "PERSON"
          let para = {taskName: "icbcSearch", key: this.key0, model: this.models, keyType: this.keyType, filter:this.filter};
          console.debug(para)
          return para;
        },
        getIcbcInfo() {
          if (this.infodata) {
            console.debug("icbc info:",this.infodata.items[0].info);
            return this.infodata.items[0].info;
          }
          else
          {
            return null;
          }
        }
      },
    methods: {

      taskFinish(data)
      {
        console.debug("taskfinish")
        this.infodata = null;
        this.infodata = data;
      }
    },

  }
</script>

<style scoped>


</style>



